<template>
  <div :class="wrapClasss">
    <!--  -->
    <textarea
      ref="textarea"
      class="mku-textarea"
      v-if="type === 'textarea'"
      :rows="rows"
      :value="currentValue"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :style="inputStyle"
      @input="handleInput"
      @focus="handleFocus"
      @blur="handleBlur"
      @keydown.enter="handleEnter"
    />

    <!--  -->
    <template v-else>
      <input
        ref="input"
        :class="inputClasss"
        :style="inputStyle"
        :type="type"
        :value="currentValue"
        :placeholder="placeholder"
        :disabled="disabled"
        :readonly="readonly"
        @input="handleInput"
        @focus="handleFocus"
        @blur="handleBlur"
        @keydown.enter="handleEnter"
      />
      <i class="mku-icon mku-input__clear" v-if="isClearable" @click="handleClear">
        &#xe633;
      </i>
    </template>
  </div>
</template>

<script>
import autosize from 'autosize';
import Emitter from '../../../utils/emitter';

export default {
  name: 'MkuInput',
  mixins: [Emitter],
  props: {
    type: {
      type: String,
      default: 'text',
      validator: (type) => ['text', 'password', 'textarea', 'url', 'email', 'date', 'number', 'tel'].includes(type),
    },
    value: {
      type: [String, Number],
      default: '',
    },
    // 
    size: {
      type: String,
      default: 'default',
      validator: (size) => ['large', 'default', 'small'].includes(size),
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    readonly: {
      type: Boolean,
      default: false,
    },
    // 
    placeholder: {
      type: String,
      default: '',
    },
    // 
    clearable: {
      type: Boolean,
      default: false,
    },
    autosize: {
      type: Boolean,
      default: false,
    },
    rows: {
      type: Number,
      default: 2,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    block: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentValue: this.value,
      isMouseover: false,
    };
  },
  watch: {
    value(val) {
      this.currentValue = val;
    },
  },
  computed: {
    isClearable() {
      if (!this.clearable || !this.currentValue.length) return false;
      return true;
    },
    wrapClasss() {
      return ['mku-input__wrap', {
        'mku-input__wrap--block': this.block,
      }];
    },
    inputClasss() {
      const prefix = 'mku-input';
      return [prefix,
        {
          [`${prefix}--large`]: this.size === 'large',
          [`${prefix}--default`]: this.size === 'default',
          [`${prefix}--small`]: this.size === 'small',
        },
      ];
    },
    inputStyle() {
      const res = {};
      if (this.type !== 'textarea') {
        res.paddingRight = this.clearable ? '22px' : '8px';
      }
      return res;
    },
  },
  mounted() {
    if (this.type === 'textarea' && this.autosize) {
      autosize(this.$refs.textarea);
    }
    if (this.autofocus) {
      const input = this.$refs.input || this.$refs.textarea;
      input.focus();
    }
  },
  methods: {
    // 
    handleInput(event) {
      const { value } = event.target;
      this.currentValue = value;
      this.$emit('input', value);
      this.dispatch('MkuFormItem', 'onFormItemChange', value);
      this.$emit('change', value);
    },
    // 
    handleFocus(event) {
      this.$emit('focus', event);
    },
    // 
    handleBlur(event) {
      this.$emit('blur', event);
      this.dispatch('MkuFormItem', 'onFormItemBlur', this.currentValue);
    },
    // 
    handleClear() {
      this.currentValue = '';
      this.$emit('input', this.currentValue);
      this.dispatch('MkuFormItem', 'onFormItemChange', this.currentValue);
      this.$emit('clear');
    },
    // 
    focus() {
      this.$refs.input.focus();
    },
    // 
    blur() {
      this.$refs.input.blur();
      this.dispatch('MkuFormItem', 'onFormItemBlur', this.value);
    },
    handleEnter(event) {
      this.$emit('enter', event, this.currentValue);
    },
  },
};
</script>
