import { isFunction } from './assist';
import { fillZero } from './tools';
/**
 * @method parseDate
 * @description 
 * @param { String, Date } time
 * @retrun { Object } 
 */
export function parseDate(time) {
  const t = time ? new Date(time) : new Date(Date.now());
  return {
    year: t.getFullYear(),
    month: t.getMonth(),
    date: t.getDate(),
    day: t.getDay(),
    hour: t.getHours(),
    minute: t.getMinutes(),
    second: t.getSeconds(),
    timeStamp: t.getTime(),
  };
}

/**
 * @method pickNumInStr
 * @description 
 * @param { String } str 
 * @param { Function } iterate 
 * @retrun { Array } 
 */
export function pickNumInStr(str, iterate) {
  const timeString = str.replace(/[^\d]+/g, () => ',');
  const nums = timeString.split(',')
    .filter((e) => e !== '')
    .map((e) => parseInt(e, 10));
  return isFunction(iterate) ? nums.map(iterate) : nums;
}

/**
 * @method generateFormatedTime
 * @description 
 * @param { String } str 
 * @param { Function } iterate 
 * @retrun { Array } 
 */
export function generateFormatedTime(time, rule) {
  const houtAt12 = (v) => (v >= 12 ? v - 12 : v);
  const t = parseDate(time);
  const maps = {
    YYYY: t.year, // eg2019
    MM: fillZero(t.month + 1), // , 2eg03
    M: t.month + 1, // , 1eg3
    DD: fillZero(t.date), // , 2eg03
    D: t.date, // , 1eg03
    HH: fillZero(t.hour), // 242
    H: t.hour, // 241
    hh: fillZero(houtAt12(t.hour)), // 122
    h: houtAt12(t.hour), // 121
    mm: fillZero(t.minute), // 2
    m: t.minute, // 1
    ss: fillZero(t.second), // 2
    s: t.second, // 2
  };
  const regStr = Object.keys(maps).map((e) => `(${e})`).join('|');
  const reg = new RegExp(regStr, 'g');
  return rule.replace(reg, (v) => maps[v]);
}

// 
export function getTime(...arg) {
  return new Date(...arg).getTime();
}
