<template>
  <div class="mku-button-wrapper" :style="buttonWrapperStyle">
    <button
      :class="['mku-button', {
        'mku-button--primary': type === 'primary',
        'mku-button--dashed': type === 'dashed',
        'mku-button--default': type === 'default',
        'mku-button--danger': type === 'danger',
        'mku-button--link': type === 'link',
        'mku-button--lg': size === 'large',
        'mku-button--sm': size === 'small',
        'mku-button--loading': loading
      }]"
      :disabled="disabled"
      @click="handleClick"
    >
      <mku-icon v-if="icon || loading" :type="buttonIcon"></mku-icon>
      <slot name="default"></slot>
    </button>
  </div>
</template>

<script>
import MkuIcon from '../../icon';

export default {
  name: 'MkuButton',
  components: {
    MkuIcon,
  },
  props: {
    // 
    type: {
      type: String,
      default: 'default',
      validator: (val) => ['primary', 'default', 'dashed', 'danger', 'link'].includes(val),
    },
    // 
    block: {
      type: Boolean,
      default: false,
    },
    // 
    size: {
      type: String,
      default: 'default',
      validator: (val) => ['large', 'default', 'small'].includes(val),
    },
    // 
    icon: {
      type: String,
      default: '',
    },
    // loading
    loading: {
      type: Boolean,
      default: false,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // linka
    href: {
      type: String,
      default: '',
    },
    // href
    target: {
      type: String,
      default: '_self',
    },
  },
  data() {
    return {

    };
  },
  watch: {
  },
  computed: {
    // buttonblocktrue
    buttonWrapperStyle() {
      const obj = {};
      if (this.block) {
        obj.display = 'block';
      }
      return obj;
    },
    // loading icon
    buttonIcon() {
      return this.loading ? 'loading' : this.icon;
    },
  },
  methods: {
    handleClick() {
      if (this.loading) return;
      if (this.href) {
        window.open(this.href, this.target);
        return;
      }
      this.$emit('click');
    },
  },
};
</script>
