<template>
  <div class="mku-tab-pane" v-if="isShow">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'MkuTabPane',
  props: {
    // value
    name: {
      type: [String, Number],
    },
    // 
    label: {
      type: [String, Number],
      default: '',
    },
    // 
    icon: {
      type: String,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  inject: ['root'],
  data() {
    return {
      width: 0, // 
      offset: 0, // 
      index: 0, // 
    };
  },
  computed: {
    // pane
    isShow() {
      if (this.root.currentName !== this.nameValue) return false;
      return this.root.panes.includes(this);
    },
    // name
    nameValue() {
      if (this.name !== undefined) return this.name;
      return this.index;
    },
  },
  created() {
    this.root.updatePanes();
  },
};
</script>
