<script>
export default {
  name: 'MkuSteps',
  props: {
    // 
    direction: {
      type: String,
      default: 'horizontal',
      validator: (val) => ['horizontal', 'vertical'].includes(val),
    },
    // 
    center: {
      type: Boolean,
      default: false,
    },
    // 
    current: {
      type: Number,
      default: 0,
    },
    // 
    currentStatus: {
      type: String,
      default: 'process',
      validator: (val) => ['wait', 'process', 'finish', 'error'].includes(val),
    },
    // 
    showIcon: {
      type: Boolean,
      default: true,
    },
  },
  provide() {
    return {
      MkuSteps: this,
    };
  },
  data() {
    return {
      steps: [],
    };
  },
  computed: {
    // stepsclass name
    stepsClasss() {
      const prefix = 'mku-steps';
      return [prefix, {
        [`${prefix}--vertical`]: this.direction === 'vertical',
        [`${prefix}--horizontal`]: this.direction === 'horizontal',
      }];
    },
  },
  render(h) {
    return h('div', {
      class: this.stepsClasss,
    }, this.$slots.default);
  },
};
</script>
