<template>
  <div class="mku-tree">
    <mku-tree-node
      v-for="(node, index) in dataCopyed" :key="index"
      :data="node"
      :label="label"
      :children="children"
      :show-checkbox="showCheckbox"
      :node-key="nodeKey"
      :default-expanded-keys="defaultExpandedKeys"
      :default-checked-keys="defaultCheckedKeys"
      :default-expand-all="defaultExpandAll"
      :accordion="accordion"
      :render-content="renderContent"
      :parent-data="dataCopyed"
    />
  </div>
</template>

<script>
import MkuTreeNode from './tree-node';
import {
  deepCopy,
  isObjectEqual,
  findComponentsDownward,
} from '../../../utils/assist';

export default {
  name: 'MkuTree',
  components: {
    MkuTreeNode,
  },
  props: {
    data: {
      type: Array,
      default: () => ([]),
    },
    label: {
      type: String,
      default: 'label',
    },
    children: {
      type: String,
      default: 'children',
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
    nodeKey: {
      type: [String, Number],
      default: '',
    },
    // 
    defaultExpandedKeys: {
      type: Array,
      default: () => [],
    },
    // 
    defaultCheckedKeys: {
      type: Array,
      default: () => [],
    },
    // 
    defaultExpandAll: {
      type: Boolean,
      default: false,
    },
    // 
    accordion: {
      type: Boolean,
      default: false,
    },
    // 
    renderContent: {
      type: Function,
    },
    // node
    expandOnClickNode: {
      type: Boolean,
      default: true,
    },
    // node
    checkOnClickNode: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      dataCopyed: [],
    };
  },
  watch: {
    data: {
      handler: 'handleDataChange',
      immediate: true,
    },
  },
  methods: {
    handleDataChange(newVal) {
      if (!newVal) {
        this.dataCopyed = [];
        return;
      }
      this.dataCopyed = deepCopy(newVal);
    },
    // emit
    emitEvent(event, ...data) {
      this.$emit(event, ...data);
    },
    // checked-change
    checkedChange(data) {
      const checkedNodes = this.getCheckedNodes();
      this.$emit('checked-change', checkedNodes, data);
    },
    /**
     * @method _iterateNodes
     * @description TreeNode
     */
    _iterateNodes(cb) {
      const nodes = findComponentsDownward(this, 'MkuTreeNode');
      if (nodes && nodes.length) {
        nodes.forEach((node) => {
          cb(node);
        });
      }
    },
    /**
     * @method setCheckedKeys
     * @description keys
     */
    setCheckedKeys(keys) {
      if (!this.showCheckbox) return;

      this._iterateNodes((node) => {
        const checked = node.key && keys.includes(node.key);
        node.handleCheckboxChange(checked);
      });
    },
    /**
     * @method setCheckedKeys
     * @description dataOrigin
     */
    setCheckedNodes(data) {
      if (!this.showCheckbox) return;

      const isEqual = (node) => data.every((value) => isObjectEqual(value, node.dataOrigin));
      const checkedFunc = (node) => node.handleCheckboxChange(isEqual(node));
      this._iterateNodes(checkedFunc);
    },
    /**
     * @method getCheckedKeys
     * @description keys
     * @returns { Array } keys
     */
    getCheckedKeys() {
      const res = [];
      this._iterateNodes((node) => {
        if (node.data.isChecked) {
          res.push(node.data[node.nodeKey]);
        }
      });
      return res;
    },
    /**
     * @method getCheckedNodes
     * @description dataOrigin
     * @returns { Array } 
     */
    getCheckedNodes() {
      const res = [];
      this._iterateNodes((node) => {
        if (node.data.isChecked) {
          res.push(node.data);
        }
      });
      return res;
    },
  },
};
</script>
