<template>
  <div :class="badgeClasss">
    <slot></slot>

    <span
      v-if="!hidden"
      :class="[dot ? 'mku-badge__dot' : 'mku-badge__value']"
      :style="badgeStyle">
      <template v-if="!dot && isShowText">
        {{formatValue}}
      </template>
    </span>
  </div>
</template>

<script>
import { isNumber } from '../../../utils/assist';

export default {
  name: 'MkuBadge',
  props: {
    // 
    value: {
      type: [Number, String],
      default: 0,
    },
    // valueNumber
    max: {
      type: Number,
    },
    // 
    hidden: {
      type: Boolean,
      default: false,
    },
    // valueNumber0
    hiddenZero: {
      type: Boolean,
      default: false,
    },
    // 
    dot: {
      type: Boolean,
      default: false,
    },
    // 
    dotSize: {
      type: [Number, String],
    },
    // xy
    offset: {
      type: Array,
    },
    // 
    type: {
      type: String,
      default: 'danger',
    },
    // type
    color: {
      type: String,
    },
  },
  computed: {
    // badgeclass name
    badgeClasss() {
      const prefix = 'mku-badge';
      return [prefix, `${prefix}--${this.type}`];
    },
    // 
    isShowText() {
      if (!this.hiddenZero) return true;
      if (!isNumber(this.value)) return true;
      return this.value > 0;
    },
    // 
    formatValue() {
      if (!isNumber(this.value)) return this.value;
      return !this.max ? this.value : this.value > this.max ? `${this.max}+` : this.value;
    },
    // 
    badgeStyle() {
      let offsetStyle = {};
      let bgStyle = {};
      let sizeStyle = {};
      if (this.offset) {
        const len = this.offset.length;
        offsetStyle = { marginLeft: this.fotmatSize(this.offset[0]) };
        if (len >= 2) {
          offsetStyle.marginTop = this.fotmatSize(this.offset[1]);
        }
      }
      if (this.color) {
        bgStyle = {
          background: this.color,
        };
      }
      if (this.dot && this.dotSize !== undefined) {
        const size = this.fotmatSize(this.dotSize);
        sizeStyle = {
          width: size,
          height: size,
        };
      }
      return { ...offsetStyle, ...bgStyle, ...sizeStyle };
    },
  },
  methods: {
    fotmatSize(val) {
      return isNumber(val) ? `${val}px` : val;
    },
  },
};
</script>
