<template>
  <div
    class="mku-backtop"
    v-show="isShow"
    :style="backtopStyle"
    @click="backToTop">
    <slot>
      <div class="mku-backtop__wrap">
        <i class="mku-icon mku-icon-backtop mku-backtop__icon"></i>
      </div>
    </slot>
  </div>
</template>

<script>
import kute from 'kute.js';
import { onEvent, offEvent } from '../../../utils/dom';
import { isString } from '../../../utils/assist';

export default {
  name: 'MkuBacktop',
  props: {
    // 
    target: {
      type: [String, Object],
    },
    // 
    scroll: {
      type: Number,
      default: 0,
    },
    // 
    duration: {
      type: Number,
      default: 300,
    },
    // btn
    height: {
      type: Number,
      default: 200,
    },
    // 
    fixed: {
      type: Boolean,
      default: true,
    },
    // 
    fixedConfig: {
      type: Object,
      default: () => ({
        right: '40px', bottom: '50px',
      }),
    },
  },
  data() {
    return {
      targetDom: null,
      scrollTop: 0,
    };
  },
  computed: {
    isShow() {
      return this.scrollTop >= this.height;
    },
    backtopStyle() {
      if (!this.fixed) return {};
      return {
        position: 'fixed',
        zIndex: 20191128,
        ...this.fixedConfig,
      };
    },
  },
  mounted() {
    this.initTarget();
    onEvent(this.targetDom, 'scroll', this.onTargetScroll);
    this.$once('hook:beforeDestroy', () => {
      offEvent(this.targetDom, 'scroll', this.onTargetScroll);
    });
  },
  methods: {
    /**
     * @method initTarget
     * @description 
     */
    initTarget() {
      this.targetDom = this.target ? (
        isString(this.target) ? document.querySelector(this.target) : this.target
      ) : document.querySelector('body');
      this.scrollTop = this.targetDom.scrollTop;
    },
    /**
     * @method onTargetScroll
     * @description 
     */
    onTargetScroll(event) {
      const { scrollTop } = event.target;
      this.scrollTop = scrollTop;
      this.$emit('scroll', scrollTop);
    },
    /**
     * @method backToTop
     * @description 
     */
    backToTop() {
      kute.to(this.targetDom, {
        // scroll: this.scroll
        scroll: this.scroll,
      }, {
        duration: this.duration,
        complete: () => {
          this.$emit('scroll-finish');
        },
      }).start();
    },
  },
};
</script>
