<template>
  <label :class="checkboxClasss">
    <span class="mku-checkbox-content">
      <span class="mku-checkbox">
        <span class="mku-checkbox-inner"></span>
        <!-- v-modelv-model -->
        <!-- https://cn.vuejs.org/v2/guide/forms.html#%E5%A4%8D%E9%80%89%E6%A1%86 -->
        <input
          type="checkbox"
          class="mku-checkbox-input"
          v-if="isGroup"
          v-model="checkboxSelectedArray"
          :value="label"
          :disabled="disabled"
          @change="handleCheckboxChange"
        />
        <input
          type="checkbox"
          class="mku-checkbox-input"
          v-else
          :checked="checkboxValue"
          :disabled="disabled"
          @change="handleCheckboxChange"
        />
      </span>
      <span>
        <slot></slot>
      </span>
    </span>
  </label>
</template>

<script>
import { findComponentUpward } from '../../../utils/assist';
import Emitter from '../../../utils/emitter';

export default {
  name: 'MkuCheckbox',
  mixins: [Emitter],
  props: {
    value: {
      type: [Boolean, String, Number],
      default: false,
    },
    trueValue: {
      type: [Boolean, String, Number],
      default: true,
    },
    falseValue: {
      type: [Boolean, String, Number],
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    indeterminate: {
      type: Boolean,
      default: false,
    },
    label: {
      type: [Boolean, String, Number],
      default: false,
    },
  },
  data() {
    return {
      checkboxValue: false, // checkbox
      checkboxSelectedArray: [], // group
      isGroup: false, // group
      parent: null, // group
    };
  },
  watch: {
    // valuecheckboxValue
    value: {
      handler(newValue) {
        const isPredict = [this.trueValue, this.falseValue].includes(newValue);
        if (isPredict) {
          this.checkboxValue = newValue === this.trueValue;
          return;
        }
        throw TypeError('trueValuefalseValue');
      },
      immediate: true,
    },
  },
  computed: {
    checkboxClasss() {
      let isChecked;
      let indeterminate;
      if (!this.isGroup) {
        isChecked = this.checkboxValue && !this.indeterminate;
        indeterminate = this.indeterminate;
      } else {
        isChecked = this.checkboxSelectedArray.includes(this.label);
        indeterminate = false;
      }
      return [
        'mku-checkbox-wrapper', {
          'mku-checkbox--checked': isChecked,
          'mku-checkbox--disabled': this.disabled,
          'mku-checkbox--indeterminate': indeterminate,
        },
      ];
    },
  },
  mounted() {
    this.parent = findComponentUpward(this, 'MkuCheckboxGroup');
    if (this.parent) {
      this.parent.updateAllCheckbox();
    }
  },
  methods: {
    handleCheckboxChange(event) {
      if (this.disabled) return;

      const { checked } = event.target;
      const value = checked ? this.trueValue : this.falseValue;
      this.checkboxValue = value;
      this.$emit('input', value);

      if (this.isGroup) {
        // group
        // changeformgroup
        this.parent.change(this.checkboxSelectedArray);
      } else {
        // checkbox
        // changeForm
        this.$emit('change', value);
        this.dispatch('MkuFormItem', 'onFormItemChange', value);
      }
    },
  },
};
</script>
