<template>

  <li :class="[menuItemClasses, className]" @click="handleClick">
    <slot></slot>
  </li>
</template>

<script>
import Emitter from '../../../utils/emitter';
import InjectMixin from '../mixins/inject';

const PREFIX_MENU_ITEM = 'mku-menu-item';

export default {
  name: 'MkuMenuItem',
  mixins: [Emitter, InjectMixin],
  props: {
    // 
    name: {
      type: String,
      required: true,
    },
    // className
    className: {
      type: String,
      default: '',
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // vue-router
    route: {
      type: [Object, String],
    },
    // vue-routerroute
    routeType: {
      type: String,
      default: 'push',
    },
    // 
    target: {
      type: String,
    },
  },
  inject: ['menu'],
  data() {
    return {
      // menu
      activedName: this.menu.defaultActive,
    };
  },
  watch: {
    'menu.activeItemName': {
      handler(n) {
        this.activedName = n;
      },
    },
  },
  computed: {
    // classNames
    menuItemClasses() {
      return [
        PREFIX_MENU_ITEM,
        {
          [`${PREFIX_MENU_ITEM}--active`]: this.isActive,
          [`${PREFIX_MENU_ITEM}--disabled`]: this.disabled,
        },
      ];
    },
    // 
    isActive() {
      return this.name === this.activedName;
    },
  },
  methods: {
    handleClick() {
      if (this.disabled) return;
      this.dispatch('MkuMenu', 'menu-item-click', this);
      this.$emit('click', this);
    },
  },
};
</script>
